import React from 'react';
import { Space, Popconfirm, Badge } from 'antd';
import moment from 'moment';
import intl from 'react-intl-universal';

import DataTable from 'components/DataTable';
import Button from 'components/Button';
import slugify from "@/utils/slugify";

import messages from './../messages';
import {check_slug} from "../service";

const icons = [
  { code: "la-tshirt", codeName: <i className="las la-tshirt"/> },
  { code: "la-shopping-bag", codeName: <i className="las la-shopping-bag"/> },
  { code: "la-gift", codeName: <i className="las la-gift"/> },
  { code: "la-shoe-prints", codeName: <i className="las la-shoe-prints"/> },
  { code: "la-medkit", codeName: <i className="las la-medkit"/> },
  { code: "la-palette", codeName: <i className="las la-palette" /> },
  { code: "la-ruler", codeName: <i className="las la-ruler" /> },
];
const formatTreeselect = (array) => {
  return array.map(item => {
    const children = (item.children && item.children.length) ? formatTreeselect(item.children) : null;
    return { value: item.id + "", title: item.name, selectable: !(item.children && item.children.length), children }
  })
}

export const createColumns = (self, categorys, brands) => {
	return [
    {
      name: 'id',
      formItem: {
        type: 'hidden'
      }
    },
    {
      title: intl.formatMessage(messages.media),
      name: "media",
      tableItem: {
        render: (url) => (url && url.length > 2) ? <img src={JSON.parse(url)[0]} alt="" width="50"/> : "No Image"
      },
      formItem: {
        type: "media",
        limit: 10,
      }
    },
    {
      title: intl.formatMessage(messages.description),
      name: "description",
      formItem: {
        type: 'editor',
      }
    },
    {
      title: intl.formatMessage(messages.name),
      name: "name",
      tableItem: {
        filterType: "input",
        sorter: true,
      },
      formItem: {
        onBlur: (e, form) => {
          const { getFieldValue, setFieldsValue, validateFields } = form.refs.form
          if (!getFieldValue("slug")) {
            setFieldsValue({slug: slugify(e.currentTarget.value)});
            validateFields(["slug"])
          }
        },
        rules: [
          {
            required: true,
            message: intl.formatMessage(messages.pleaseEnterName),
          }
        ]
      }
    },
    {
      title: intl.formatMessage(messages.slug),
      name: "slug",
      formItem: {
        rules: [
          ({ getFieldValue }) => ({
            validator: async (rule, value) => {
              if (!!value || value.trim() !== "") {
                const data = await check_slug({
                  id: getFieldValue("id"),
                  slug: value,
                  model: "products"
                });
                if (data) return Promise.resolve()
                else return Promise.reject(intl.formatMessage(messages.theSlugHasAlreadyBeenTaken));
              } else return Promise.resolve()
            },
          }),
          {
            required: true,
            message: intl.formatMessage(messages.pleaseEnterSlug)
          }
        ]
      }
    },
    {
      title: intl.formatMessage(messages.summary),
      name: "summary",
      formItem: {
        type: 'textarea',
      }
    },
    {
      title: intl.formatMessage(messages.featured),
      name: "featured",
      formItem: {
        type: "switch"
      }
    },
    {
      title: intl.formatMessage(messages.category),
      name: "categories.category_id",
      tableItem: {
        filters: categorys.map((item) => ({ code: item.id, codeName: item.title })),
        filterType: "checkbox",
        render: (text, record) => record.categories.map(item => item.name).join(", ")
      }
    },
    {
      title: intl.formatMessage(messages.category),
      name: "category_id",
      formItem: {
        type: "treeselect",
        multiple: true,
        treeData: formatTreeselect(categorys),
        rules: [
          {
            required: true,
            message: intl.formatMessage(messages.pleaseEnterCategory)
          }
        ]
      },
    },
    {
      title: intl.formatMessage(messages.brand),
      name: 'brands.name|posts.brand_id',
      tableItem: {
        sorter: true,
        render(text, record) {
          return record.brand.name;
        }
      },
    },
    {
      title: intl.formatMessage(messages.brand),
      name: 'brand_id',
      dict: brands.map(item => ({ code: item.id, codeName: item.name })),
      formItem: {
        type: 'select',
        rules: [
          {
            required: true,
            message: intl.formatMessage(messages.pleaseEnterBrand)
          }
        ]
      },
    },
    {
      title: intl.formatMessage(messages.quantity),
      name: "quantity",
      tableItem: {}
    },
    {
      title: intl.formatMessage(messages.price),
      name: "price",
      tableItem: {
        sorter: true,
        render: text => text.replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      },
      formItem: {
        type: "number",
        min: 0,
        max: 999999999999,
        formatter: value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ','),
        parser: value => value.replace(/\s|(,*)/g, ''),
        rules: [
          {
            required: true,
            message: intl.formatMessage(messages.pleaseEnterPrice)
          }
        ]
      }
    },
    {
      title: intl.formatMessage(messages.salePrice),
      name: "sale_price",
      tableItem: {
        sorter: true,
        render: text => text && text.replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      },
      formItem: {
        type: "number",
        min: 0,
        max: 999999999999,
        formatter: value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ','),
        parser: value => value.replace(/\s|(,*)/g, ''),
      }
    },
    {
      title: intl.formatMessage(messages.status),
      name: "status",
      formItem: {
        type: "switch",
        initialValue: true,
      }
    },
    {
      title: intl.formatMessage(messages.updatedAt),
      name: "updated_at",
      tableItem: {
        filterType: "datetime",
        sorter: true,
        render: (text) => moment(text).format("DD/MM/YYYY")
      },
    },
    {
      title: intl.formatMessage(messages.operating),
      tableItem: {
        width: 180,
        render: (text, record) => {
          let number = 0;
          record.product_attributes.map(item => number += item.quantity )
          record.quantity = number
          record.category_id = record.categories.map(item => item.category_id + "");
          return (
            <DataTable.Oper>
              <Space>
                <Button tooltip={intl.formatMessage(messages.modify)} onClick={() => self.onUpdate(record)}>
                  <i className="las la-edit" />
                </Button>
                <Badge count={record.product_attributes.length}>
                  <Button
                    tooltip={intl.formatMessage(messages.attribute)}
                    onClick={() => self.onAttribute(record)}
                    type={record.product_attributes.length > 0 ? "primary" : ""}
                  >
                    <i className="las la-cogs" />
                  </Button>
                </Badge>
                <Button
                  tooltip={intl.formatMessage(messages.featured)}
                  type={record.featured !== 0 ? "primary" : ""}
                  onClick={() => self.handlePatch([{ id: record.id, featured: !record.featured }])}
                >
                  <i className="las la-award" />
                </Button>
                <Button
                  type={record.status === 0 ? "primary" : ""}
                  danger={record.status === 0}
                  tooltip={intl.formatMessage(messages.status)}
                  onClick={() => self.handlePatch([{ id: record.id, status: !record.status }])}
                >
                  <i className={`las ${ record.status === 0 ? "la-low-vision" : "la-eye"}`} />
                </Button>
                <Popconfirm
                  title={intl.formatMessage(messages.areYouSure)}
                  icon={<i className="las la-question-circle" />}
                  onConfirm={() => self.handleDelete([record])}
                >
                  <Button tooltip={intl.formatMessage(messages.delete)}>
                    <i className="las la-trash-alt" />
                  </Button>
                </Popconfirm>
              </Space>
            </DataTable.Oper>
          )
        }
      }
    }
  ]
};
