import React from 'react';
import { connect } from 'dva';
import { Layout, Button } from 'antd';
import intl from 'react-intl-universal';

import BaseComponent from 'components/BaseComponent';
import Toolbar from 'components/Toolbar';
import DataTable from 'components/DataTable';
import { ModalForm, ModalDragList, ModalArrayForm, } from 'components/Modal';
import { createColumns, createFormProductAttribute, createFormAttributeDrag, createFormCategoryDrag, createFormBrandDrag } from './columns';
import messages from './../messages';
import './index.less';

const { Content, Header, Footer } = Layout;
const { Pagination } = DataTable;

@connect(({ product, loading }) => ({
  product,
  loading: loading.models.product
}))
class Product extends BaseComponent {
  constructor(props) {
    super(props);
    this.state = {
      record: null,
      visible: false,
      visibleAttribute: false,
      visibleDragCategory: false,
      visibleDragAttribute: false,
      visibleDragBrand: false,
      rows: [],
      productId: null,
      productAttributes: [],
    }
  }

  handleDelete = records => {
    const { rows } = this.state;
    const { dispatch } = this.props;

    dispatch({
      type: 'product/remove',
      payload: {
        records,
        success: () => {
          // If the operation is successful, exclude the deleted row in the selected row
          this.setState({
            rows: rows.filter(
              item => !records.some(jItem => jItem.rowKey === item.rowKey)
            )
          });
        }
      }
    });
  };

  handlePatch = (records) => {
    const { dispatch } = this.props;
    dispatch({ type: 'product/patch', payload: { records }});
  }

  onAttribute = (records) => {
    this.setState({visibleAttribute: true, productId: records.id, productAttributes: records.product_attributes })
  }

  render() {
    const { product, loading, dispatch, history, location } = this.props;
    const { pageData, categorys, attributes, brands  } = product;
    const columns = createColumns(this, categorys, brands);
    const { rows, record, visible, visibleAttribute, visibleDragCategory, visibleDragAttribute, visibleDragBrand, productId, productAttributes } = this.state;

    if (Object.keys(pageData.sorts).length || Object.keys(pageData.filters).length) {
      columns.map(item => {
        if (pageData.sorts[item.name] || pageData.filters[item.name]) {
          item.tableItem = {
            ...item.tableItem,
            sortOrder: pageData.sorts[item.name],
            filteredValue: pageData.filters[item.name]
          }
        }
        return item;
      })
    }

    const dataTableProps = {
      loading,
      columns,
      rowKey: 'id',
      dataItems: pageData,
      selectType: 'checkbox',
      showNum: false,
      isScroll: true,
      selectedRowKeys: rows.map(item => item.id),
      onChange: (page) => history.replace(location.pathname +pageData.toParams(page)),
      onSelect: (keys, rows) => this.setState({ rows })
    };

    const modalFormProps = {
      loading,
      record,
      visible,
      columns,
      modalOpts: {
        width: 1200
      },
      formOpts: {
        cols: {
          xs: 24,
          md: 12,
          xl: 12
        }
      },
      onCancel: () => {
        this.setState({
          record: null,
          visible: false
        });
      },
      onSubmit: values => {
        values = !record ? values : {...values, id: record.id}
        dispatch({
          type: 'product/save',
          payload: {
            values,
            success: () => {
              this.setState({
                record: null,
                visible: false
              });
            }
          }
        });
      }
    };

    const modalArrayFormProps = {
      loading,
      title: "Attribute",
      array: productAttributes,
      visible: visibleAttribute,
      columns: createFormProductAttribute(attributes),
      modalOpts: {
        width: 1200
      },
      formOpts: {
        cols: {
          xs: 24,
          md: 12,
          xl: 6
        },
      },
      onAdd: () => {
        productAttributes.push({ id: -1, product_id: productId, attributes_ids: undefined,quantity: undefined, price: undefined, sale_price: undefined })
        this.setState({ productAttributes })
      },
      onRemove: (index) => {
        if (productAttributes[index].id > 0) {
          dispatch({ type: 'product/removeProductAttribute', payload: productAttributes[index].id });
        }
        productAttributes.splice(index, 1);
        this.setState({ productAttributes })
      },
      onCancel: () => {
        this.setState({ visibleAttribute: false, productId: null, productAttributes: []});
      },
      onSubmit: async payload => {
        dispatch({ type: 'product/changeProductAttribute', payload });
        this.setState({ visibleAttribute: false, productId: null, productAttributes: []});
      },
    };

    const dragProps = (columns, data, key, visible, visibleKey, title, maxDepth, name) => {
      return {
        loading,
        columns: columns,
        data,
        visible,
        title,
        maxDepth,
        modalOpts: {
          width: 700
        },
        onSubmit: (data, value) => {
          if (value) dispatch({ type: `product/change${name}`, payload: { value, data }});
          else dispatch({ type: "product/@change", payload: { [key]: data }})
        },
        onDrag: (data) => {
          dispatch({ type: "product/@change", payload: { [key]: data }})
        },
        onCancel: () => {
          this.setState({ [visibleKey]: false });
        },
        onSave: () => {
          dispatch({ type: `product/change${name}`, payload: { value: null, data } })
          this.setState({ [visibleKey]: false });
        },
        onDelete: (id) => {
          if (id > 0) {
            dispatch({ type: `product/remove${name}`, payload: id })
          } else {
            const deleteItem = (array) => {
              for (let i = 0; i < array.length; i++) {
                if (array[i].id === id) {
                  array.splice(i, 1);
                  i--;
                } else if (array[i].children){
                  array[i].children = deleteItem(array[i].children);
                }
              }
              return array;
            }
            dispatch({ type: "product/@change", payload: deleteItem(data) })
          }
        }
      };
    }
    const modalDrapCategoryProps = dragProps(createFormCategoryDrag(), categorys, "categorys", visibleDragCategory, "visibleDragCategory", intl.formatMessage(messages.category), 2,"Category");
    const modalDrapAttributeProps = dragProps(createFormAttributeDrag(), attributes, "attributes", visibleDragAttribute, "visibleDragAttribute", intl.formatMessage(messages.attribute), 2,"Attribute");
    const modalDrapBrandProps = dragProps(createFormBrandDrag(), brands, "brands", visibleDragBrand, "visibleDragBrand", intl.formatMessage(messages.brand), 1,"Brand");

    return (
      <Layout className="full-layout product-page">
        <Header>
          <Toolbar
            appendLeft={
              <Button.Group>
                <Button type="primary" icon={<i className="las la-plus" />} onClick={this.onAdd}>
                  {intl.formatMessage(messages.addNew)}
                </Button>
                <Button
                  type="primary"
                  disabled={!rows.length}
                  onClick={e => this.onDelete(rows)}
                  icon={<i className="las la-trash-alt" />}
                  danger={rows.length}
                >
                  {intl.formatMessage(messages.delete)}
                </Button>
              </Button.Group>
              }
          >
            <Button.Group>
              <Button
                type="primary"
                icon={<i className="las la-sitemap" />} onClick={() => this.setState({ visibleDragCategory: true })}
              >
                {intl.formatMessage(messages.category)}
              </Button>
              <Button
                type="primary"
                icon={<i className="las la-cogs" />} onClick={() => this.setState({ visibleDragAttribute: true })}
              >
                {intl.formatMessage(messages.attribute)}
              </Button>
              <Button
                type="primary"
                icon={<i className="las la-industry" />} onClick={() => this.setState({ visibleDragBrand: true })}
              >
                {intl.formatMessage(messages.brand)}
              </Button>
            </Button.Group>
          </Toolbar>
        </Header>
        <Content className="has-footer">
          <DataTable {...dataTableProps} />
        </Content>
        <Footer>
          <Pagination {...dataTableProps} />
        </Footer>
        <ModalForm {...modalFormProps} />
        <ModalArrayForm {...modalArrayFormProps} />
        <ModalDragList {...modalDrapCategoryProps} />
        <ModalDragList {...modalDrapAttributeProps} />
        <ModalDragList {...modalDrapBrandProps} />
      </Layout>
    );
  }
}

export default Product;
